import { NextRequest } from 'next/server';
import { createLogger } from '@/lib/logger';
import { withCronJobTracking, type CronJobContext } from '@/lib/cron-job-wrapper';

/**
 * Dogfood Cron Endpoint
 * 
 * Vercel Cron Job that exercises NexFlow end-to-end:
 * - Queries discovery actions
 * - Picks random actions
 * - Routes test payments across facilitators
 * - Logs structured dogfood data for AI-optimized SMF
 * 
 * Schedule: Every 5 minutes (configured in vercel.json)
 * 
 * Environment Variables:
 *   - DOGFOOD_ENABLED: "true" to enable
 *   - DOGFOOD_NETWORK: Target network (default: eip155:8453)
 *   - DOGFOOD_MODE: "demo" or "live" (default: "demo")
 *   - CRON_SECRET: Optional secret for authorization
 */

const logger = createLogger({ component: 'DogfoodCron' });

// =============================================================================
// CONFIGURATION
// =============================================================================

const DOGFOOD_NETWORK = process.env.DOGFOOD_NETWORK ?? "eip155:8453";
const DOGFOOD_MODE = (process.env.DOGFOOD_MODE ?? "demo") as "demo" | "live";
const DISCOVERY_URL = process.env.DOGFOOD_DISCOVERY_URL ?? "https://discovery.nexflowapp.app/api/discovery/actions";
const ROUTER_URL = process.env.DOGFOOD_ROUTER_URL ?? "https://www.nexflowapp.app/api/x402/router/execute";

// Facilitator strategies
const FACILITATOR_STRATEGIES = ["cdp", "payai", "x402rs", "auto"] as const;

// =============================================================================
// TYPES
// =============================================================================

interface DogfoodLogEntry {
  timestamp: string;
  agentType: "dogfood";
  facilitatorId: string;
  actionId: string;
  actionUrl?: string;
  network: string;
  asset: string;
  status: "success" | "error" | "timeout" | "skipped";
  latencyMs: number;
  errorCode?: string;
  errorMessage?: string;
  httpStatus?: number;
  mode: "demo" | "live";
}

interface DiscoveryAction {
  id: string;
  name: string;
  domain: string;
  endpoint: string;
  methods: string[];
  pricing?: {
    amount: string;
    asset: string;
    network: string;
  };
}

interface RouterResponse {
  ok: boolean;
  mode: string;
  result?: {
    status: "success" | "error";
    facilitatorId?: string;
    errorCode?: string;
  };
  error?: string;
}

// =============================================================================
// UTILITIES
// =============================================================================

function pickFrom<T>(arr: readonly T[]): T {
  return arr[Math.floor(Math.random() * arr.length)];
}

function pickN<T>(arr: T[], n: number): T[] {
  const shuffled = [...arr].sort(() => Math.random() - 0.5);
  return shuffled.slice(0, Math.min(n, arr.length));
}

// =============================================================================
// DOGFOOD LOGIC
// =============================================================================

async function fetchDiscoveryActions(): Promise<DiscoveryAction[]> {
  const url = new URL(DISCOVERY_URL);
  url.searchParams.set('network', DOGFOOD_NETWORK);

  const response = await fetch(url.toString(), {
    method: 'GET',
    headers: { 'Accept': 'application/json' },
    signal: AbortSignal.timeout(10000),
  });

  if (!response.ok) {
    throw new Error(`Discovery API returned ${response.status}`);
  }

  const data = await response.json() as { actions?: DiscoveryAction[] };
  return data.actions || [];
}

async function executeRouterRequest(
  action: DiscoveryAction,
  facilitatorStrategy: string
): Promise<DogfoodLogEntry> {
  const startTime = Date.now();
  
  const payload = {
    resource: action.endpoint,
    method: action.methods[0] || "POST",
    maxSpend: "0.05",
    asset: action.pricing?.asset || "USDC",
    network: DOGFOOD_NETWORK,
    facilitatorStrategy,
    client: "dogfood-cron",
    mode: DOGFOOD_MODE,
    agentType: "dogfood",
    useCase: "cron-health-check",
  };

  try {
    const response = await fetch(ROUTER_URL, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'X-Agent-Type': 'dogfood',
      },
      body: JSON.stringify(payload),
      signal: AbortSignal.timeout(30000),
    });

    const latencyMs = Date.now() - startTime;
    const responseBody = await response.json() as RouterResponse;

    const success = response.ok && responseBody.ok && 
      (responseBody.result?.status === 'success' || responseBody.mode === 'demo');

    return {
      timestamp: new Date().toISOString(),
      agentType: "dogfood",
      facilitatorId: responseBody.result?.facilitatorId || facilitatorStrategy,
      actionId: action.id,
      actionUrl: action.endpoint,
      network: DOGFOOD_NETWORK,
      asset: action.pricing?.asset || "USDC",
      status: success ? "success" : "error",
      latencyMs,
      httpStatus: response.status,
      mode: DOGFOOD_MODE,
      ...(responseBody.result?.errorCode && { errorCode: responseBody.result.errorCode }),
      ...(responseBody.error && { errorMessage: responseBody.error }),
    };
  } catch (error) {
    const latencyMs = Date.now() - startTime;
    const isTimeout = error instanceof Error && error.name === 'TimeoutError';
    
    return {
      timestamp: new Date().toISOString(),
      agentType: "dogfood",
      facilitatorId: facilitatorStrategy,
      actionId: action.id,
      actionUrl: action.endpoint,
      network: DOGFOOD_NETWORK,
      asset: action.pricing?.asset || "USDC",
      status: isTimeout ? "timeout" : "error",
      latencyMs,
      errorCode: isTimeout ? "TIMEOUT" : "NETWORK_ERROR",
      errorMessage: error instanceof Error ? error.message : "Unknown error",
      mode: DOGFOOD_MODE,
    };
  }
}

// =============================================================================
// CRON HANDLER
// =============================================================================

async function dogfoodHandler(
  _request: NextRequest,
  context: CronJobContext
): Promise<{ ok: boolean; [key: string]: any }> {
  const { runId, requestId } = context;
  const results: DogfoodLogEntry[] = [];

  logger.info({
    runId,
    requestId,
    network: DOGFOOD_NETWORK,
    mode: DOGFOOD_MODE,
    msg: 'Starting dogfood run',
  });

  // Fetch discovery actions
  const actions = await fetchDiscoveryActions();
  
  if (actions.length === 0) {
    logger.info({ runId, msg: `No actions found for network ${DOGFOOD_NETWORK}` });
    return {
      ok: true,
      message: `No actions found for network ${DOGFOOD_NETWORK}`,
      skipped: true,
    };
  }

  // Pick 1-2 random actions
  const numActions = Math.random() > 0.5 ? 2 : 1;
  const selectedActions = pickN(actions, numActions);

  // Execute router requests
  for (const action of selectedActions) {
    const facilitatorStrategy = pickFrom(FACILITATOR_STRATEGIES);
    const entry = await executeRouterRequest(action, facilitatorStrategy);
    
    // Log structured entry
    logger.info({
      runId,
      ...entry,
      msg: `Dogfood route ${entry.status}`,
    });
    results.push(entry);
  }

  const successCount = results.filter(r => r.status === 'success').length;

  logger.info({
    runId,
    actionsProcessed: results.length,
    successCount,
    msg: 'Dogfood run completed',
  });

  return {
    ok: true,
    message: 'Dogfood run completed',
    actionsProcessed: results.length,
    successCount,
    results,
  };
}

// =============================================================================
// EXPORTS
// =============================================================================

export const dynamic = 'force-dynamic';
export const maxDuration = 60;

export const GET = withCronJobTracking(dogfoodHandler, {
  jobId: 'dogfood',
  enabledEnvVar: 'DOGFOOD_ENABLED',
  timeout: 55000, // 55s (leave buffer for wrapper overhead)
  minInterval: 4 * 60 * 1000, // 4 minutes (schedule is every 5 min)
});
